// 统一管理接口
import { get, post } from './http';

// 接口域名
const baseUrl = {
  v_1: '/apis', // 默认请求路径
  v_2: 'http://daily-d.tuiqiucai.com/sportdata-api/sportdata/f/'   // 比分数据请求接口路径
}


// 首页接口
const homePageApi = {
  template (params) {
    return get(baseUrl.v_1 + 'rest?method=ifeng.template&v=1.0', params);
  }
};

// 竞篮接口
const betBasketBall = {
  getBetListData (params){
    return get(baseUrl.v_1 + 'rest?method=ifeng.sports.lc.race.get&v=1.0', params);
  },
  maxCombina (params) {
    return get(baseUrl.v_1 + 'rest?method=ifeng.sports.jclc.maxCombina&v=1.0', params);
  }
};

export default {
  homePageApi,
  betBasketBall
}




