<template>
  <div class="webViewPage">
    <div class="header">
      <goBack class="btn lf1" url="/homePage" query=""></goBack>
      <p class="title">{{title}}</p>
    </div>
    <div class="content">
      <iframe :src="url"></iframe>
    </div>
  </div>
</template>

<script>
import goBack from '../common/goBack'
export default {
  name: 'webViewPage',
  components:{goBack},
  created(){
    this.title = this.$route.query.title;
    this.url   = this.$route.query.url;
  },
  data() {
    return {
      title: '',
      src: ''
    };
  },
  methods: {

  }
}
</script>

<style lang="scss" type="text/scss" scoped>
  body::-webkit-scrollbar {/*隐藏滚轮*/
    display: none;
  }
  .webViewPage{
    height: 100vh;
    position: relative;
  }
</style>



