<template>
  <div class="matchPage">
    <div class="header">
      <div class="shopInfo">
        <div class="logo fl"></div>
        <p class="name fl">推球实体店</p>
      </div>
    </div>
    <div class="content hasFooter" @scroll="scroll" id="tabsContent">
      <p v-for="(item,index) in 100">match{{item}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'matchPage',
  data() {
    return {
    };
  },
  methods: {
    scroll(event) {
      localStorage.setItem('matchPage',event.target.scrollTop);
    }
  }
}
</script>

<style>
</style>



